.frame {
    background: #cbc4c4;
    width: 90%;
    margin-left: 5%;
    margin-top: 10px;
    margin-bottom: 10px;
    border: 2px solid black;
    border-radius: 10px;
    box-shadow: 0px 2px 5px rgba(69, 67, 67, 0.787);
    display: flex;
    flex-direction: row;
    height: 130px;
}

.avatar {
    width: 100px;
    height: 100px;
    border-radius: 10px 0px 0px 0px;
}

p {
    margin: 0;
    margin-top: 2px;
}

.description {
    margin-left: 20px;;
}

.followFrame {
    display: flex;
    flex-direction: column;
}

.follow {
    background-color: green;
    height: 30px;
    border-radius: 0px 0px 0px 8px;
    border: 1px solid black;
    border-bottom: none;
    border-left: none;
    color: white;
    cursor: pointer;
}

.unfollow {
    background-color: red;
    height: 30px;
    border-radius: 0px 0px 0px 8px;
    border: 1px solid black;
    border-bottom: none;
    border-left: none;
    color: white;
    cursor: pointer;
}